import React, {createRef, useEffect, useState} from 'react'
import {Card, CardActions, CardActionArea, CardContent, CardMedia, Typography, Button} from '@material-ui/core'
import useStyles from './card.styles';


const NewsCard = ({ article: {description, title, publishedAt, url, urlToImage, source}, index, activeArticle }) => {
    const classes = useStyles();
    const [elRefs, setElRefs] = useState([]);
    const scrollToRef = (ref) => window.scroll(0, ref.current.offsetTop - 50);

    useEffect(() => {
        window.scroll(0, 0);
        setElRefs((refs) => Array(20).fill().map((_, j) => refs[j] || createRef()));
    }, []);

    useEffect(() => {
        if (index === activeArticle && elRefs[activeArticle]) {
        scrollToRef(elRefs[activeArticle]);
        }
    }, [index, activeArticle, elRefs]);

    return (
        <Card ref={elRefs[index]} className={ activeArticle === index ? classes.activeCard : classes.card}>
            <CardActionArea href={url} target="_blank">
                <CardMedia
                    className={classes.media}
                    image={urlToImage || 'https://www.industry.gov.au/sites/default/files/August%202018/image/news-placeholder-738.png'}
                />
                <div className={classes.details}>
                    <Typography variant="body2" color="textSecondary" component="h2">{(new Date(publishedAt)).toDateString()}</Typography>
                    <Typography variant="body2" color="textSecondary" component="h2">{source.name}</Typography>
                </div>
                <Typography className={classes.title} variant="h5" gutterBottom>{title}</Typography>
                <CardContent>
                    <Typography variant="body2" component="p" color="textSecondary">{description}</Typography>
                </CardContent>
            </CardActionArea>
            <CardActions className={classes.cardActions}>
                <Button size="small" color="primary">Learn More</Button>
                <Typography variant="h6" color="textSecondary">{index + 1}</Typography>
            </CardActions>
        </Card>
    )
}

export default NewsCard
